import React from "react";
import toast from "react-hot-toast";

// 
export const showToast = {
  success: (message) => {
    toast.success(message);
  },
  
  error: (message) => {
    toast.error(message);
  },
  
  info: (message) => {
    toast(message, {
      icon: "",
      style: {
        background: "var(--bg-card)",
        color: "var(--text-primary)",
        border: "1px solid var(--border-color)",
        borderLeftColor: "var(--color-info)",
        borderLeftWidth: "4px",
      },
      ariaProps: {
        role: "status",
        "aria-live": "polite",
        "aria-label": `: ${message}`
      },
    });
  },
  
  warning: (message) => {
    toast(message, {
      icon: "",
      style: {
        background: "var(--bg-card)",
        color: "var(--text-primary)", 
        border: "1px solid var(--border-color)",
        borderLeftColor: "var(--color-warning)",
        borderLeftWidth: "4px",
      },
      ariaProps: {
        role: "alert",
        "aria-live": "assertive",
        "aria-label": `: ${message}`
      },
    });
  },
  
  loading: (message) => {
    return toast.loading(message);
  },
  
  dismiss: (toastId) => {
    toast.dismiss(toastId);
  },
};

// 
export const showConfirm = (message, options = {}) => {
  return new Promise((resolve) => {
    const {
      confirmText = "",
      cancelText = "",
      confirmStyle = "danger", // "danger" | "primary"
    } = options;

    toast(
      (t) => (
        <div 
          style={{ minWidth: "250px" }}
          onKeyDown={(e) => {
            if (e.key === 'Escape') {
              toast.dismiss(t.id);
              resolve(false);
            } else if (e.key === 'Enter') {
              toast.dismiss(t.id);
              resolve(true);
            }
          }}
          tabIndex={-1}
          autoFocus
        >
          <div style={{ 
            marginBottom: "12px", 
            fontSize: "var(--font-size-sm)",
            color: "var(--text-primary)",
          }}>
            {message}
          </div>
          <div style={{ display: "flex", gap: "8px", justifyContent: "flex-end" }}>
            <button
              style={{
                padding: "6px 12px",
                backgroundColor: "var(--bg-input)",
                color: "var(--text-secondary)",
                border: "1px solid var(--border-color)",
                borderRadius: "var(--border-radius)",
                cursor: "pointer",
                fontSize: "var(--font-size-xs)",
                transition: "var(--transition-fast)",
                outline: "none",
              }}
              onClick={() => {
                toast.dismiss(t.id);
                resolve(false);
              }}
              onMouseOver={(e) => {
                e.target.style.backgroundColor = "var(--bg-tertiary)";
              }}
              onMouseOut={(e) => {
                e.target.style.backgroundColor = "var(--bg-input)";
              }}
              onFocus={(e) => {
                e.target.style.outline = "2px solid var(--color-primary)";
                e.target.style.outlineOffset = "2px";
              }}
              onBlur={(e) => {
                e.target.style.outline = "none";
              }}
            >
              {cancelText}
            </button>
            <button
              style={{
                padding: "6px 12px",
                backgroundColor: confirmStyle === "danger" ? "var(--color-danger)" : "var(--color-primary)",
                color: "white",
                border: "none",
                borderRadius: "var(--border-radius)",
                cursor: "pointer",
                fontSize: "var(--font-size-xs)",
                transition: "var(--transition-fast)",
                outline: "none",
              }}
              onClick={() => {
                toast.dismiss(t.id);
                resolve(true);
              }}
              onFocus={(e) => {
                e.target.style.outline = "2px solid white";
                e.target.style.outlineOffset = "2px";
              }}
              onBlur={(e) => {
                e.target.style.outline = "none";
              }}
              autoFocus
            >
              {confirmText}
            </button>
          </div>
        </div>
      ),
      {
        duration: Infinity,
        style: {
          background: "var(--bg-card)",
          color: "var(--text-primary)",
          border: "1px solid var(--border-color)",
          borderRadius: "var(--border-radius)",
          padding: "16px",
          boxShadow: "var(--shadow-sm)",
        },
        ariaProps: {
          role: "alertdialog",
          "aria-modal": "true",
          "aria-label": ""
        },
      }
    );
  });
};

//  alert 
/**
 *  -  alert() 
 * @param {string} message - 
 */
export const showAlert = (message) => {
  showToast.info(message);
};

//  confirm 
/**
 *  -  Promise confirm() 
 * @param {string} message - 
 * @returns {Promise<boolean>}  Promise
 */
export const showConfirmDialog = (message) => {
  return showConfirm(message);
};