import React, { createContext, useState, useEffect } from 'react';

export const LanguageContext = createContext();

export const LanguageProvider = ({ children }) => {
  const [language, setLanguage] = useState('zh');
  const [translations, setTranslations] = useState({});

  useEffect(() => {
    // 
    const loadTranslations = async () => {
      try {
        const translationModule = await import(`../locales/${language}.json`);
        setTranslations(translationModule.default);
      } catch (error) {
        console.error('Error loading translations:', error);
        // 
        try {
          const fallbackLang = language === 'zh' ? 'en' : 'zh';
          const fallbackModule = await import(`../locales/${fallbackLang}.json`);
          setTranslations(fallbackModule.default);
          console.warn(`Loaded fallback translations for ${fallbackLang}`);
        } catch (fallbackError) {
          console.error('Failed to load fallback translations:', fallbackError);
          setTranslations({});
        }
      }
    };

    loadTranslations();
  }, [language]);

  useEffect(() => {
    // 
    const savedLanguage = localStorage.getItem('language');
    if (savedLanguage && ['zh', 'en'].includes(savedLanguage)) {
      setLanguage(savedLanguage);
    } else {
      // 
      const detectLanguageFromBrowser = () => {
        // 
        const languages = [
          navigator.language,
          navigator.languages?.[0],
          navigator.userLanguage, // IE
          navigator.browserLanguage, // IE
          'en' // fallback
        ].filter(Boolean);
        
        // 
        for (const lang of languages) {
          const normalizedLang = lang.toLowerCase();
          if (normalizedLang.startsWith('zh')) {
            return 'zh';
          }
          if (normalizedLang.startsWith('en')) {
            return 'en';
          }
        }
        
        return 'en'; // 
      };
      
      setLanguage(detectLanguageFromBrowser());
    }
  }, []);

  useEffect(() => {
    // 
    localStorage.setItem('language', language);

    // HTML lang
    document.documentElement.lang = language === 'zh' ? 'zh-CN' : 'en';
  }, [language]);

  const toggleLanguage = () => {
    setLanguage(prev => prev === 'zh' ? 'en' : 'zh');
  };

  // 
  const t = (key, params = {}) => {
    try {
      let translation;
      
      //  'common.button.save'
      if (key.includes('.')) {
        const keys = key.split('.');
        translation = translations;
        
        for (const k of keys) {
          if (translation && typeof translation === 'object') {
            translation = translation[k];
          } else {
            translation = undefined;
            break;
          }
        }
      } else {
        // 
        translation = translations[key];
      }
      
      // fallback
      if (translation === undefined || translation === null) {
        translation = key; // keyfallback
      }
      
      // 
      if (typeof translation === 'string' && Object.keys(params).length > 0) {
        Object.keys(params).forEach(param => {
          const placeholder = new RegExp(`\\{${param}\\}`, 'g');
          translation = translation.replace(placeholder, params[param]);
        });
      }
      
      return translation;
    } catch (error) {
      console.warn(`Translation error for key: ${key}`, error);
      return key; // key
    }
  };

  const value = {
    language,
    toggleLanguage,
    t,
    isZh: language === 'zh'
  };

  return (
    <LanguageContext.Provider value={value}>
      {children}
    </LanguageContext.Provider>
  );
};
